@import "./styles/variables.scss";
@import "./styles/responsive.scss";

.App {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  padding: 4rem 1rem;
  position: relative;

  .Background {
    z-index: -1;
    position: fixed;
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .Logo {
      height: 80vmin;
      opacity: 0.3;
      pointer-events: none;

      @media (prefers-reduced-motion: no-preference) {
        animation: App-logo-spin infinite 24s linear;
      }
    }
  }

  .Content {
    width: 100%;
    display: flex;
    flex-direction: column;

    .Title {
      width: 100%;
    }

    .Title h1 {
      text-align: center;
      font-size: $LG;
      line-height: 150%;
      text-transform: capitalize;
      font-weight: 400;
      font-family: "Press Start 2P", cursive;
      background: linear-gradient(164deg, var(--my-blue), var(--my-pink));
      background-clip: text;
      -webkit-background-clip: text;
      -moz-background-clip: text;
      color: transparent;
    }

    .ChallengesList {
      padding: 4rem 0;
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 3rem;
    }
  }

  .Footer {
    p {
      text-align: center;
      font-size: $RG;
      word-spacing: 0.2rem;

      a {
        text-decoration: none;
        background: linear-gradient(164deg, var(--my-blue), var(--my-pink));
        background-clip: text;
        -webkit-background-clip: text;
        -moz-background-clip: text;
        color: transparent;
      }
    }
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
