@import "../styles/variables.scss";
@import "../styles/responsive.scss";

.CardOuter {
  width: 100%;
  max-width: 350px;
  height: 100%;
  padding: 0.3rem;
  position: relative;
  border-radius: 2rem;
  background: linear-gradient(45deg, var(--my-blue), var(--my-pink));

  &:hover {
    background: linear-gradient(
      45deg,
      var(--my-bright-blue),
      var(--my-bright-pink)
    );
  }

  .CardInner {
    border-radius: 2rem;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--primary);
    position: relative;

    .ImageContainer {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      &:hover,
      &:active {
        img {
          animation: imageSlide 4s linear infinite alternate;
        }
      }

      img {
        opacity: 0.9;
        border-top-right-radius: 2rem;
        border-top-left-radius: 2rem;
        width: 100%;
        height: 230px;
        object-fit: cover;
        object-position: top;
      }
    }

    .Info {
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 1.5rem 2rem;

      .Title {
        font-size: $RG;
        font-weight: 400;
        margin-bottom: 1.5rem;
        text-transform: capitalize;
      }

      .ActionButtons {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        gap: 2rem;

        a {
          text-transform: capitalize;
          text-decoration: none;
          font-size: $SM;
          color: var(--light);
          background: linear-gradient(45deg, var(--my-blue), var(--my-pink));
          padding: 0.5rem 1.5rem;
          border-radius: 3px;

          &:hover {
            background: linear-gradient(
              65deg,
              var(--my-bright-blue),
              var(--my-bright-pink)
            );
          }
        }
      }
    }
  }
}

@keyframes imageSlide {
  0%,
  10% {
    object-position: top;
  }

  85%,
  100% {
    object-position: bottom;
  }
}
